<template>
  <v-app>
    <v-app-bar app color="error"><v-toolbar-title>Sismik Aktivite Takipçisi</v-toolbar-title></v-app-bar>
    <v-main>
      <v-container>
        <div id="info" class="my-15">
          <p>Türkiye'de günde iki kez <b style="color: darkred;">4+</b>, altı günde bir kez de <b style="color: darkred;">5+</b> şiddetinde bir deprem oluyor.</p>
          <v-card color="error" class="pa-5 mt-10">
            <v-card-text>Bunun sebebi Türkiye'nin;
                <ul class="ml-10 my-3">
                  <li>Kuzey Anadolu Fay Hattı</li>
                  <li>Doğu Anadolu Fay Hattı</li>
                  <li>Batı Anadolu Fay Hattı</li>
                </ul>
                Olmak üzere 3 farklı fay hattının üzerinde bulunması.
              </v-card-text>
            </v-card>
            <p>Bu web sayfasında <b>AFAD resmi kaynaklarına göre</b> ülkemizde en son yaşanan sismik sarsıntıları görebilirsiniz.</p>
            <p>Yapan: <a href="https://furkanmuslu.github.io" style="color: darkred;" class="font-weight-bold">Furkan Muslu</a></p>

        </div>
        <v-timeline side="end" style="width: 100%;">
          <v-timeline-item dot-color="pink" icon="mdi-circle-slice-8" size="small" v-for="(earthquake, index) in earthquakes" :key="index">
        
            <div>
              <div class="me-4"><b class="text-h6 font-weight-bold">{{ earthquake.time }}</b> {{ earthquake.date }} </div>
              <div><b>Konum:</b> {{ earthquake.location }}</div>
              <div><b>Şiddet: </b> {{ earthquake.magnitude }}</div>
              <div><b>Derinlik: </b> {{ earthquake.depth }} km</div>
            </div>
          </v-timeline-item>


        </v-timeline>
      </v-container>
    </v-main>
  </v-app>
</template>

<script setup>
import { ref } from "vue";

const earthquakes = ref([]);

fetch("https://deprem.afad.gov.tr/EventData/GetEventsByFilter", {
  method: "POST",
  headers: {
    "accept": "application/json, text/plain, */*",
    "accept-language": "en-US,en;q=0.5",
    "cache-control": "no-cache, no-store, must-revalidate",
    "content-type": "application/json",
    "strict-transport-security": "max-age=16070400; includeSubDomains",
    "x-content-type-options": "nosniff",
    "x-frame-options": "deny",
    "x-xss-protection": "1; mode=block"
  },
  body: JSON.stringify({
    "EventSearchFilterList": [
      { "FilterType": 9, "Value": new Date().toISOString() },
      { "FilterType": 8, "Value": new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString() }
    ],
    "Skip": 0,
    "Take": 15,
    "SortDescriptor": { "field": "eventDate", "dir": "desc" }
  })
})
.then(response => {
  if (!response.ok) {
    throw new Error('Network response was not ok ' + response.statusText);
  }
  return response.json();
})
.then(data => {
  // process all items in eventList
  console.log(data)
  data.eventList.forEach(item => {
    earthquakes.value.push({
      time: item.eventDate.slice(11, 16),
      date: item.eventDate.slice(0, 10),
      magnitude: item.magnitude,
      depth: item.depth,
      location: item.location
    });
  })
})
.catch(error => {
  console.error('There was a problem with the fetch operation:', error);
});

</script>

<style scoped>
p {
  margin-top: 10px;
}
</style>